@import 'scss/libs/variables.scss';

.btn {
	padding: 12px;
	display: flex;
	align-items: center;
	background-color: transparent;
	font-size: 18px;
	color: #006edb;
	border: 1px solid rgba(0, 110, 219, 0.2);
	border-radius: 10px;
	margin-left: 20px;

	
	&:hover {
		background-color: rgba(0, 110, 219, 0.2);
		border: 1px solid rgba(0, 110, 219, 1);
	}
	
	&:active {
		background-color: rgba(0, 110, 219, 0.3);
		border: 1px solid rgba(0, 110, 219, 1);
	}
}

.icon {
	width: 20px;
	height: 20px;
	margin-right: 5px;
}