@import 'scss/libs/variables.scss';

.root {
	display: block;
}

.item {
	display: flex;
	padding: 20px 0;
	align-items: center;
	position: relative;
	cursor: pointer;

	&:after {
		content: '';
		border-bottom: 1px solid $lightGrey;
		width: 100%;
		display: block;
		position: absolute;
		bottom: 0;
	}

	&:hover {
		background-color: rgba(195, 208, 216, 0.3);
		display: flex;
	}
}